section {
  position: relative;
}
section > .inner {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.kv {
  width: 100%;
  height: 750px;
}
.kv img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: right 0;
}

.lead_area {
  margin: 50px 0 70px;
}
.lead_area > .inner {
  display: flex;
  justify-content: space-between;
}
.lead_area > .inner .col_ttl h1 {
  font-size: 35px;
  font-weight: 500;
  line-height: 1;
  padding-bottom: 15px;
  border-bottom: 4px solid #000000;
}
.lead_area > .inner .col_ttl h1 span {
  position: relative;
  display: inline-block;
}
.lead_area > .inner .col_ttl h1 span:after {
  position: absolute;
  right: 0;
  bottom: -19px;
  width: 100%;
  height: 4px;
  content: "";
  background: #e60012;
}
.lead_area > .inner .col_ttl p.en {
  font-size: 13px;
  margin-top: 10px;
}
.lead_area > .inner .col_txt {
  width: 100%;
  max-width: 340px;
  text-align: left;
}
.lead_area > .inner .col_txt h2 {
  font-size: 18px;
}
.lead_area > .inner .col_txt p {
  font-size: 12px;
}

.links_area {
  width: 100%;
}
.links_area ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.links_area ul li {
  width: 33.3333333333%;
  aspect-ratio: 10/7;
}
.links_area ul li a {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  padding: 30px;
}
.links_area ul li a .base {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.links_area ul li a dl {
  position: relative;
  width: 265px;
  text-align: left;
  color: #ffffff;
}
.links_area ul li a dl dt {
  font-size: 22px;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ffffff;
}
.links_area ul li a dl dt span {
  font-size: 14px;
  margin-left: 20px;
}
.links_area ul li a dl dd {
  font-size: 15px;
  line-height: 1.6;
}
.links_area .products_modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 200;
  display: none;
}
.links_area .products_modal .modal_wrap {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.links_area .products_modal .modal_wrap .inner {
  background: #fff;
  padding: 50px;
}